declare var $: any
import { Component } from '@angular/core';
import { Events, IonicPage, NavController, NavParams } from 'ionic-angular';
import { HttpServicesProvider } from '../../../providers/http-services';
import { PhoneDeviceProvider } from '../../../providers/phone-device';
import { StorageProvider } from '../../../providers/storage';


/**
 * Generated class for the LlmtagsummaryspecialtyPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-lmtagsummaryspecialty',
  templateUrl: 'lmtagsummaryspecialty.html',
})
export class LmtagsummaryspecialtyPage {
  public list = [] // 列表
  public projectid = ''

  constructor(public navCtrl: NavController, public navParams: NavParams, public httpService: HttpServicesProvider, public phoneDevice: PhoneDeviceProvider, public events: Events, public storage: StorageProvider) {
    this.phoneDevice.phoneBacktoPop(this.navCtrl);
  }

  // ionViewDidLoad() {
  //   console.log('ionViewDidLoad LmtagsummaryPage');
  //   // this.initData()
  // }
  ionViewWillEnter() { //当将要进入页面时触发
    this.projectid = this.storage.get(StorageProvider.LM_PROJECTID);
    console.log(`%c%s`, `color:blue`, `[Debug] ~ LmtagsummaryspecialtyPage ~ ionViewWillEnter ~ this.projectid`, this.projectid)
    this.list = [];
    // this.initproject();
    this.initData()
  }
  // initproject() {
  //   this.httpService.get('/query/lmpj/lmpjProject/headlist?format=list', null, (result) => {
  //     this.projectid = result[0]['projectid'];
  //     this.initData();
  //   });
  // }
  initData() {
    this.list = [];
    let url = `/lmtagSummarySpecialty/list?sort=summaryid&order=desc&_search=false&rows=15&page=1&sidx=&sord=asc&projectid=${this.projectid}`
    this.httpService.get(url, null, (result) => {
      if (result != null) {
        var rows = result.rows;
        this.list = rows;
        console.log(this.list);
      }
    });
  }

  handleInfo(item) {
    // const { projectname, specialtyname, code, summaryid, wfstatusname } = item
    this.navCtrl.push('LmtagsummaryspecialtyinfoPage', { item })
  }

  doRefresh(refresher) {
    this.initData()
    setTimeout(() => {
      refresher.complete(); //停止下拉刷新
    }, 1500)
  }

}



